import { Component, Input } from '@angular/core';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
    selector: 'wf-icon',
    templateUrl: './wf-icon.component.html',
    styleUrls: ['./wf-icon.component.scss'],
    host: { 'class': 'mat-typography' }
})
export class WfIconComponent {
    @Input() iconName: string
    @Input() iconSize: 'small' | 'medium' | 'large' = 'medium'

    constructor(
        private iconRegistry: MatIconRegistry,
        private sanitizer: DomSanitizer
    ) {
        const registerIcon = (iconName: string, icon: any) => {
            this.iconRegistry.addSvgIconLiteral(iconName, sanitizer.bypassSecurityTrustHtml(icon));
        };
        registerIcon(IconType.INCIDENT, Icons.incident.icon);
        registerIcon(IconType.INCIDENT_WITH_STATUS, Icons.incident.icon);
        registerIcon(IconType.INCIDENT_AGENCY_ASSIST, Icons.incidentAgencyAssist.icon);
        registerIcon(IconType.INCIDENT_FIELD_ACTIVITY, Icons.incidentFieldActivity.icon);
        registerIcon(IconType.REPORT_OF_FIRE, Icons.rof.icon);
        registerIcon(IconType.NO_MORE_REPORT_OF_FIRE, Icons.nrof.icon);
        registerIcon(IconType.NO_MORE_REPORT_OF_FIRE_DARK, Icons.nrof.dark);
        registerIcon(IconType.NO_MORE_REPORT_OF_FIRE_LIGHT, Icons.nrof.light);
        // registerIcon(IconType.INCIDENT + this.HALO_SUFFIX, Icons.incident.halo);
        // registerIcon(IconType.REPORT_OF_FIRE + this.HALO_SUFFIX, Icons.rof.halo);
        registerIcon(IconType.PLACE_NAME_SEARCH, Icons.placeNameSearch.icon);
        registerIcon(IconType.POINT_ID, Icons.pointId.icon);
        registerIcon(IconType.FILTER_CANCEL, Icons.filterCancel.icon);
    }

    get actualIconName() {
        if (this.iconName.startsWith('wf/'))
            return this.iconName.substr(3)

        return this.iconName
    }

    get isWfApplicationIcon() {
        return this.iconName.startsWith('wf/')
    }

    get isMatIcon() {
        return !this.isWfApplicationIcon
    }

    get sizeSmall() {
        return this.iconSize == 'small'
    }

    get sizeMedium() {
        return this.iconSize == 'medium'
    }

    get sizeLarge() {
        return this.iconSize == 'large'
    }

}


enum IconType {
    INCIDENT = 'incident',
    INCIDENT_WITH_STATUS = 'incident-with-status',
    INCIDENT_AGENCY_ASSIST = 'incident-agency-assist',
    INCIDENT_FIELD_ACTIVITY = 'incident-field-activity',
    REPORT_OF_FIRE = 'report-of-fire',
    NO_MORE_REPORT_OF_FIRE = 'no-more-report-of-fire',
    NO_MORE_REPORT_OF_FIRE_DARK = 'no-more-report-of-fire-dark',
    NO_MORE_REPORT_OF_FIRE_LIGHT = 'no-more-report-of-fire-light',
    PLACE_NAME_SEARCH = 'place-name-search',
    POINT_ID = 'point-id',
    FILTER_CANCEL = 'filter-cancel'
}

enum IconSize {
    DEFAULT = 'toolbar',
    TOOLBAR = 'toolbar',
    LIST_SMALL = 'list-small',
    LIST_LARGE = 'list-large',
}

